// 10px padding and margins
.pad {
    padding: 10px;
}

.margin {
    margin: 10px;
}

.margin-bottom {
    margin-bottom: 20px;
}

.margin-bottom-none {
    margin-bottom: 0;
}

.margin-r-5 {
    margin-right: 5px;
}

// Display inline
.inline {
    display: inline;
}

// Description Blocks
.description-block {
    display: block;
    margin: 10px 0;
    text-align: center;
    &.margin-bottom {
        margin-bottom: 25px;
    }
    > .description-header {
        margin: 0;
        padding: 0;
        font-weight: 600;
        font-size: 16px;
    }
    > .description-text {
        text-transform: uppercase;
    }
}

