    padding: 0 !important;
}

// Remove margins
.no-margin {
    margin: 0 !important;
}

// Remove box shadow
.no-shadow {
    box-shadow: none !important;
}

// Unstyled List
.list-unstyled {
    list-style: none;
    margin: 0;
    padding: 0;
}

.list-group-unbordered {
    > .list-group-item {
        border-left: 0;
        border-right: 0;
        border-radius: 0;
        padding-left: 0;
        padding-right: 0;
    }
}

// Remove border radius
.flat {
    .border-radius(0) !important;
}

.text-bold {
    &, &.table td, &.table th {
        font-weight: 700;
    }
}

.text-sm {
    font-size: 12px;
}

// _fix for sparkline tooltip
.jqstooltip {
