.Switch {
  position: relative;
  display: inline-block;
  font-size: 1.6em;
  font-weight: bold;
  color: #ccc;
  text-shadow: 0px 1px 1px rgba(255, 255, 255, 0.8);
  height: 22px;
  padding: 6px 6px 5px 6px;
  border: 1px solid #ccc;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background: #ececec;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1), inset 0px 1px 3px 0px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
}
body.IE7 .Switch {
  width: 78px;
}
.Switch span {
  display: inline-block;
  width: 35px;
}
.Switch span.On {
  color: #33d2da;
}
.Switch .Toggle {
  position: absolute;
  top: 1px;
  width: 37px;
  height: 25px;
  border: 1px solid #ccc;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 4px;
  background: #fff;
  background: -moz-linear-gradient(to bottom, #ececec 0%, #ffffff 100%);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #ececec), color-stop(100%, #ffffff));
  background: -webkit-linear-gradient(to bottom, #ececec 0%, #ffffff 100%);
  background: -o-linear-gradient(to bottom, #ececec 0%, #ffffff 100%);
  background: -ms-linear-gradient(to bottom, #ececec 0%, #ffffff 100%);
  background: linear-gradient(to bottom, #ececec 0%, #ffffff 100%);
  box-shadow: inset 0px 1px 0px 0px rgba(255, 255, 255, 0.5);
  z-index: 999;
  -webkit-transition: all 0.15s ease-in-out;
  -moz-transition: all 0.15s ease-in-out;
  -o-transition: all 0.15s ease-in-out;
  -ms-transition: all 0.15s ease-in-out;
}
.Switch.On .Toggle {
  left: 2%;
}
.Switch.Off .Toggle {
  left: 54%;
}
/* Round Switch */
.Switch.Round {
  padding: 0px 26px;
  border-radius: 40px;
}
body.IE7 .Switch.Round {
  width: 1px;
}
.Switch.Round .Toggle {
  border-radius: 40px;
  width: 18px;
  height: 18px;
}
.Switch.Round.Off .Toggle {
  left: 1px;
  background: #4CAF50;
}
.Switch.Round.On .Toggle {
  left: 63%;
