.register-page .bg-image {
  width: 100vw;
  height: 100vh;
  background: url('../img/bg-login.jpg');
  background-size: cover;
  position: absolute;
}
.login-page .login-box,
.register-page .login-box,
.login-page .register-box,
.register-page .register-box {
  width: 450px;
  height: 100vh;
  position: absolute;
  background: #FFF;
  right: 0px;
  padding: 40px;
}
.login-page .login-logo,
.register-page .login-logo,
.login-page .register-logo,
.register-page .register-logo {
  font-size: 35px;
  text-align: left;
  margin-bottom: 25px;
  font-weight: 300;
}
.login-page .login-box-msg,
.register-page .login-box-msg,
.login-page .register-box-msg,
.register-page .register-box-msg {
  margin: 0;
  text-align: left;
  padding: 0 20px 20px 0px;
  color: grey;
}
.login-page .login-box-footer,
.register-page .login-box-footer,
.login-page .register-box-footer,
.register-page .register-box-footer {
  position: absolute;
  bottom: 0px;
  padding: 20px 30px 20px 10px;
}
