  width: 100%;
  margin-top: 16px;
  font-weight: bold;
}
.profile2 .desc {
  margin-top: 6px;
}
.profile2 .row.stats {
  display: block;
}
.profile2 .row.stats .stat {
  font-size: 18px;
}
