/* ================= Chat Boxes ================= */
.chatbox.t1 {
  text-align: left;
  white-space: initial;
  position: fixed;
  bottom: 0px;
  right: 10px;
  width: 240px;
  height: 300px;
  font-family: 'Open Sans', sans-serif;
  border: 1px solid #605ca8;
  border-bottom: 0px;
  font-size: 12px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: #f9f9f9;
  z-index: 10;
  transition: all 0.2s ease;
}
.chatbox.t1 .header {
  height: 25px;
  line-height: 25px;
  background-color: #605ca8;
  color: #fff;
  font-weight: bold;
  width: 100%;
  float: left;
  font-size: 12px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  cursor: pointer;
}
.chatbox.t1 .header p {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  float: left;
  padding-left: 5px;
  vertical-align: baseline;
}
.chatbox.t1 .header p i {
  font-size: 10px;
  vertical-align: text-top;
}
.chatbox.t1 .header p span {
  font-size: 13px;
  font-weight: normal;
}
.chatbox.t1 .header div {
  float: right;
  text-decoration: none;
  color: #EEE;
  display: block;
  margin-right: 4px;
  padding-left: 4px;
  padding-right: 4px;
}
.chatbox.t1 .header div:hover {
  color: #FFF;
}
.chatbox.t1 .header .close_chatbox:hover img {
  opacity: 0.5;
}
.chatbox.t1 .header .close_chatbox img {
  vertical-align: middle !important;
}
.chatbox.t1 .header img.sts_img {
  vertical-align: text-bottom;
}
.chatbox.t1.minimize {
  height: 25px;
}
.chatbox.t1.minimize .chat_area,
.chatbox.t1.minimize .chat_info,
.chatbox.t1.minimize .chat_message {
  height: 0px;
}
.chat-sidebar-bg {
  position: fixed;
  z-index: 10;
  bottom: 0;
}
/* ================= Chat Sidebar ================= */
.chat-sidebar-bg,
.chat-sidebar {
  top: 0;
  right: -44px;
  width: 44px;
  height: 100%;
  -webkit-transition: right 0.3s ease-in-out;
  -o-transition: right 0.3s ease-in-out;
  transition: right 0.3s ease-in-out;
}
.chat-sidebar {
  position: absolute;
  padding-top: 50px;
  z-index: 11;
}
@media (max-width: 768px) {
  .chat-sidebar {
    padding-top: 100px;
  }
}
.chat-sidebar > .chat-sidebar-users {
  height: 100%;
  list-style: none;
  padding: 0px;
  margin: 0px;
  overflow: scroll;
  position: fixed;
}
.chat-sidebar > .chat-sidebar-users > li > a {
  display: block;
  padding: 7px;
  color: #FFF;
  cursor: pointer;
}
.chat-sidebar > .chat-sidebar-users > li > a:before,
.chat-sidebar > .chat-sidebar-users > li > a:after {
  content: " ";
  display: table;
}
.chat-sidebar > .chat-sidebar-users > li > a:after {
  clear: both;
}
.chat-sidebar > .chat-sidebar-users > li > a:hover {
  background: #394e57;
}
.chat-sidebar > .chat-sidebar-users .user-icon {
  float: left;
  width: 29px;
  height: 29px;
  border-radius: 50%;
  text-align: center;
  line-height: 29px;
}
.chat-sidebar > .chat-sidebar-users .user-image {
  float: left;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
  border: solid 2px rgba(255, 255, 255, 0.46);
}
.chat-sidebar > .chat-sidebar-users .user-live-icon {
  font-size: 9px;
  display: block;
  position: absolute;
  margin-top: -2px;
  margin-left: -2px;
}
.chat-sidebar.chat-sidebar-open,
.chat-sidebar.chat-sidebar-open + .chat-sidebar-bg {
  right: 0;
}
.chat-sidebar-open .chat-sidebar-bg,
.chat-sidebar-open .chat-sidebar {
  right: 0;
}
@media (min-width: 768px) {
  .chat-sidebar-open .content-wrapper,
  .chat-sidebar-open .right-side,
  .chat-sidebar-open .main-footer {
    margin-right: 44px;
  }
}
