/*
 * Component: Timeline
 * -------------------
 */

.timeline {
    position: relative;
    margin: 0 0 50px 0;
    padding: 0;
    list-style: none;

    // The line
    &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        width: 4px;
        background: #ddd;
        left: 31px;
        margin: 0;
        .border-radius(2px);
    }

    > li {
        position: relative;
        margin-right: 10px;
        margin-bottom: 15px;
        .clearfix();

        // The content
        > .timeline-item {
            .box-shadow(@box-boxshadow);
            .border-radius(@box-border-radius);
            margin-top: 0;
            background: #fff;
            color: #444;
            margin-left: 60px;
            margin-right: 15px;
            padding: 0;
            position: relative;

            // The time and header
            > .time {
                color: #999;
                float: right;
                padding: 10px;
                font-size: 12px;
            }
            > .timeline-header {
                margin: 0;
                color: #555;
                border-bottom: 1px solid @box-border-color;
                padding: 10px;
                font-size: 16px;
                line-height: 1.1;
                > a {
                    font-weight: 600;
                }
            }
            // Item body and footer
            > .timeline-body, > .timeline-footer {
                padding: 10px;

                ul.log-changes {
                    list-style: none;
                    padding: 10px;

                    li {
                        margin-bottom: 10px;
                        .key {
                            display: inline-block;
                            width: 100px;
                            margin-right: 20px;
                        }
                        .old {

                        }
                        i.fa {
                            margin-left: 10px;
                            margin-right: 10px;
                            color: #f1ca4e;
                        }
                        .new {

                        }
                    }
                }
            }

        }

        // The icons
        > .fa,
        > .glyphicon,
        > .ion {
            width: 30px;
            height: 30px;
            font-size: 15px;
            line-height: 30px;
            position: absolute;
            color: #666;
            background: @gray;
            border-radius: 50%;
            text-align: center;
            left: 18px;
            top: 0;
        }
    }

    // Time label
    > .time-label {
        > span {
            font-weight: 600;
            padding: 5px;
            display: inline-block;
            background-color: #fff;

            .border-radius(4px);
        }
    }
}

.timeline-inverse {
    > li {
        > .timeline-item {
            background: #f0f0f0;
            border: 1px solid #ddd;
            .box-shadow(none);
            > .timeline-header {
                border-bottom-color: #ddd;
            }
        }
    }
}
